@import url(https://fonts.googleapis.com/css2?family=Playball&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Playball&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: url(/my-todo-app/static/media/background.a240b231.jpg) no-repeat;
  background-size: 100vw minmax(100vh, auto);
}

h1 {
  color: #B975FF;
  text-align: center;
  font-family: 'Playball', cursive;
  text-shadow: 2px 1px #798BFF;
}

.taskbox {
    display: flex;
    justify-content: center;
}

::-webkit-input-placeholder {
    font-family: 'Pacifico', cursive;  
}

:-ms-input-placeholder {
    font-family: 'Pacifico', cursive;  
}

::-ms-input-placeholder {
    font-family: 'Pacifico', cursive;  
}

::placeholder {
    font-family: 'Pacifico', cursive;  
}

.green {
    color: #66c99d;
}

.addTask {
    background-color: #798BFF;
    color: #000;
    font-size: 18px;
    border: 1px solid #798BFF;
    font-family: 'Pacifico', cursive;
    border-bottom-right-radius: 3px;
    border-top-right-radius: 3px;
}

.textTask {
    border-bottom-left-radius: 3px;
    border-top-left-radius: 3px;
    border-color: rgba(0, 0, 0, 0);
}
li {
    list-style-type: none;
    text-align: center;
    font-family: 'Playball', cursive;
    background-color: rgba(255, 255, 255, 0.45);
    border: 1px solid #f5bf0f;
    color: #000;
    text-shadow: 1px 1px #f5bf0f;
    font-size: 24px;
    font-weight: bold;
    padding: 8px 0;
    margin: 10px 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    width: 100%;    
}

.checkItem {
    float: left;
    margin: 0 10px;
    cursor: pointer;
    color: #66c99d;
}

.removeItem {
    float: right;
    margin: 0 10px;
    cursor: pointer;
    color: rgb(255, 0, 32);
}

  .box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 40vw;
}

